#interest {
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 100%, rgba(31, 41, 55, 1) 100%, rgba(31, 41, 55, 1) 100%);
}

@media screen and (min-width: 640px) {
    #project_intro_p{
        margin-top: 400px;
    }

    #background_image {
        
        height: 120vh;
    }
    
    .project_card {
        width: 90vw;
        height: 90vh;
        font-size: small;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        padding-top: 10px;
    }
    
    .image {
        width: 80px;
        margin-left: -80px;
        margin-top: -90px;
    }

    #future {
        padding-left: 25vw;
    }

    p{
        font-size: 16px;
    }
}

@media screen and (min-width: 768px) {
    #project_image{
        height: 75vh;
    }
    .project_card {
        width: 50vw;
        height: 60vh;
    }
    
    .image {
        width: 125px;
        margin-top: -70px;
        margin-left: -50px;
    }
    #footer_logo{
        font-size: 150px;
    }

    h2{
        font-size: 50px;
    }

    h3{
        font-size: 35px;
    }
}

@media screen and (min-width: 1024px) {
    #interest {
        background: rgb(255, 255, 255);
        background: linear-gradient(180deg, rgba(255, 255, 255, 1) 70%, rgba(31, 41, 55, 1) 70%, rgba(31, 41, 55, 1) 100%);
    }
    
    .image {
        width: 150px;
        margin-left: -10px;
    }

    .project_headline{
        height: 550px;
      }

      p{
        font-size: 17px;
      }

    
}

@media screen and (min-width: 1280px) {
    #project_image{
        width: 100vw;
        height: 75vh;
    }
}

/*MAX WIDTH*/


@media screen and (max-width: 640px) {
    #project_image{
        background-size: 200vw auto;
    }
    p{
        font-size: 15px;
    }
}


@media screen and (max-width: 768px) {
    #project_image{
        background-size: 150vw auto;
    }
    
    .title_text::before {
        height: 7px;
        bottom: 5px;
    }
    
    #interest {
        background: #fff;
    }
    
    #footer_logo{
        font-size: 100px;
    }
    
    h2{
        font-size: 40px;
    }
    
    h3{
        font-size: 25px;
    }
    
}
/* FUTURE SECTION */


@media screen and (max-width: 1024px) {
    .project_headline{
        height: 300px;
      }
}